import React, { Component } from 'react';
//  ECharts 
import * as echarts from 'echarts';

class EchartsTest extends Component {
    componentDidMount(){
        var chartDom = document.getElementById('main');
        var myChart = echarts.init(chartDom);
        var option;
        option = {
            tooltip: {
                trigger: 'item'
            },
            legend: {
                bottom: '0%',
                left: 'center'
            },
            series: [
                {
                    name: '',
                    type: 'pie',
                    radius: ['40%', '70%'],
                    avoidLabelOverlap: false,
                    data: [
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                        {value: 0, name: ''},
                    ]
                },
            ]
        };
        option && myChart.setOption(option);
    }
    render(){
        return (
            <div id="main" style={{ width: 1170, height: 450 }}></div>
        );
    }
}
export default EchartsTest