import $ from "jquery";
import * as echarts from "echarts";
import { useEffect } from "react";

export default function A() {
  useEffect(() => {
    var ROOT_PATH =
      "https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples";

    var chartDom = document.getElementById("main");
    var chartDom1 = document.getElementById("min");
    var myChart = echarts.init(chartDom);
    var myChart1 = echarts.init(chartDom1);
    var option;

    myChart.showLoading();
    myChart1.showLoading();

    $.get(ROOT_PATH + "/data/asset/geo/HK.json", function (geoJson) {
      myChart.hideLoading();
      myChart1.hideLoading();

      echarts.registerMap("HK", geoJson);

      myChart.setOption(
        (option = {
          tooltip: {
            trigger: "item",
            formatter: "{b}<br/>{c} (p / km2)",
          },
          toolbox: {
            show: true,
            orient: "vertical",
            left: "right",
            top: "center",
            feature: {
              dataView: { readOnly: false },
              restore: {},
              saveAsImage: {},
            },
          },
          visualMap: {
            min: 800,
            max: 50000,
            text: ["High", "Low"],
            realtime: false,
            calculable: true,
            inRange: {
              color: ["lightskyblue", "yellow", "orangered"],
            },
          },
          series: [
            {
              name: "18",
              type: "map",
              mapType: "HK", // 
              label: {
                show: true,
              },
              data: [
                { name: "", value: 20057.34 },
                { name: "", value: 15477.48 },
                { name: "", value: 31686.1 },
                { name: "", value: 6992.6 },
                { name: "", value: 44045.49 },
                { name: "", value: 40689.64 },
                { name: "", value: 37659.78 },
                { name: "", value: 45180.97 },
                { name: "", value: 55204.26 },
                { name: "", value: 21900.9 },
                { name: "", value: 4918.26 },
                { name: "", value: 5881.84 },
                { name: "", value: 4178.01 },
                { name: "", value: 2227.92 },
                { name: "", value: 2180.98 },
                { name: "", value: 9172.94 },
                { name: "", value: 3368 },
                { name: "", value: 806.98 },
              ],
              // 
              nameMap: {
                "Central and Western": "",
                Eastern: "",
                Islands: "",
                "Kowloon City": "",
                "Kwai Tsing": "",
                "Kwun Tong": "",
                North: "",
                "Sai Kung": "",
                "Sha Tin": "",
                "Sham Shui Po": "",
                Southern: "",
                "Tai Po": "",
                "Tsuen Wan": "",
                "Tuen Mun": "",
                "Wan Chai": "",
                "Wong Tai Sin": "",
                "Yau Tsim Mong": "",
                "Yuen Long": "",
              },
            },
          ],
        })
      );
      myChart1.setOption(
        (option = {
          tooltip: {
            trigger: "item",
            formatter: "{b}<br/>{c} (p / km2)",
          },
          toolbox: {
            show: true,
            orient: "vertical",
            left: "right",
            top: "center",
            feature: {
              dataView: { readOnly: false },
              restore: {},
              saveAsImage: {},
            },
          },
          visualMap: {
            min: 800,
            max: 50000,
            text: ["High", "Low"],
            realtime: false,
            calculable: true,
            inRange: {
              color: ["lightskyblue", "yellow", "orangered"],
            },
          },
          series: [
            {
              name: "18",
              type: "map",
              mapType: "HK", // 
              label: {
                show: true,
              },
              data: [
                { name: "", value: 20057.34 },
                { name: "", value: 15477.48 },
                { name: "", value: 31686.1 },
                { name: "", value: 6992.6 },
                { name: "", value: 44045.49 },
                { name: "", value: 40689.64 },
                { name: "", value: 37659.78 },
                { name: "", value: 45180.97 },
                { name: "", value: 55204.26 },
                { name: "", value: 21900.9 },
                { name: "", value: 4918.26 },
                { name: "", value: 5881.84 },
                { name: "", value: 4178.01 },
                { name: "", value: 2227.92 },
                { name: "", value: 2180.98 },
                { name: "", value: 9172.94 },
                { name: "", value: 3368 },
                { name: "", value: 806.98 },
              ],
              // 
              nameMap: {
                "Central and Western": "",
                Eastern: "",
                Islands: "",
                "Kowloon City": "",
                "Kwai Tsing": "",
                "Kwun Tong": "",
                North: "",
                "Sai Kung": "",
                "Sha Tin": "",
                "Sham Shui Po": "",
                Southern: "",
                "Tai Po": "",
                "Tsuen Wan": "",
                "Tuen Mun": "",
                "Wan Chai": "",
                "Wong Tai Sin": "",
                "Yau Tsim Mong": "",
                "Yuen Long": "",
              },
            },
          ],
        })
      );
    });
    
  });
  return (
    <div className="com4">
      <div id="main"></div>
      <div id="min"></div>
    </div>
  );
}
